<template>
  <div id='Search' class="page-container">
    <div id='topnav'>
      <div class="topnavfix">
        <div class="toplogin">
          <a @click="gotoIndex()" class="homeicon">
            <img src="/static/icon/icon-home.png" alt="" style="width:20px;">
          </a>
        </div>

        <div class="topsearch">
          <form @submit.prevent="submit($event)" class="topsearchform">
            <div class="topsearchbox">
              <div class="topsearchmain">
                <span class="topsearchicon">
                  <img src="/static/icon/icon-search.png" alt="" style="width:13px;">
                </span>
                <input type="search" name="keyword" placeholder="搜索您感兴趣的课程" class="topsearchinput" v-model="keyword">
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>

    <!--<div class="topclear"></div>-->

    <!-- 搜索页筛选 -->
    <div class="filterbox">
      <div class="ui container">
        <div class="ui two column grid">
          <div @click='switchfilterclassUI()' class="column">
            {{getisfree()}}
            <i class="icondown"></i>
          </div>
          <div @click='switchfiltercategoryUI()' class="column">
            <!-- {{category[categoryid-1].name}} -->
            {{getcategoryname()}}
            <i class="icondown"></i>
          </div>
        </div>
      </div>
    </div>

    <!-- 课程筛选框 -->
    <div v-show='filterclassUI' class="filterclass">
      <a @click='filterclassnum(0)' :class="{'cur':isfreenum==0}">全部课程</a>
      <a @click='filterclassnum(1)' :class="{'cur':isfreenum==1}">免费课程</a>
      <a @click='filterclassnum(2)' :class="{'cur':isfreenum==2}">入门VIP</a>
      <div @click='switchfilterclassUI()' class="filtermark"></div>
    </div>

    <!-- 分类筛选框 -->
    <div v-show='filtercategoryUI' class="filtercategory">
      <div class="categorynav">
        <ul class="categorymenu" >
          <a v-for='(item,key) in category'>
            <li @click='filtercategorynum(item.id)' class="categorychild" :class="{'cur':key+1==categoryid}">
              <img :src="'/static/icon'+item.img" alt="">{{item.name}}
            </li>
          </a>
        </ul>
      </div>
    </div>

    <div v-show='filtercategoryUI' @click='switchfiltercategoryUI()' class="categorymark"></div>

    <!-- 结果列表 -->

    <div class="search-list">
      <div class="ui container">
        <a v-for='item in article' @click='toarticle(item.id)' class="articlecontent">
          <div class="articleleft">
            <div class="articleleftbox">
              <div class="articleleftimg">
<!--                <img :src="'/static'+item.img" alt="">-->
                <img :src="'https://cdn.jsdelivr.net/gh/tlx159/imgholder@master'+item.img" alt="">-->

              </div>
              <div class="articleleftlearned">
                {{item.people}}人已学习
              </div>
            </div>
          </div>
          <div class="articleright">
            <div>
              <div class="articlerighttitle">
                {{item.title}}
              </div>
              <!-- <div class="articlerightinfo">
                在线观看下载（所有VIP可看）
              </div> -->
            </div>
            <div class="articlerightbottom">
              <span class="articlerightbottomtime">
                {{item.createtime}}发布
              </span>

              <span v-if='item.isFree==1' class="articlerightbottompricefree">
                免费
              </span>
              <span v-if='item.isFree==2' class="articlerightbottompricevip">
                入门
              </span>

            </div>
          </div>
        </a>

      </div>
    </div>

    <div class="clear"></div>
    <div class="bottom_tip">

        <p v-if="isfinishload==false">加载中……</p>
        <p v-if="isfullresult==true">没有更多数据了</p>


    </div>

    <!-- 没有搜索结果 -->
    <div v-if='nosearch==true' class="nosearchbox">
      <div class="nosearchbd">
        <h3>没有找到任何课程~</h3>
        <p>
          换个关键词或者<a @click="gotoIndex()" class="nosearchgotoindex">去首页看看…</a>
        </p>
      </div>
    </div>


  </div>
</template>

<script>

export default {
  name: 'Search',
  data () {
    return {
      article:[],
      category:[],
      filtercategoryUI:false,
      filterclassUI:false,
      loginType:false,
      username:'',
      password:'',
      categoryid:1,
      isfreenum:0,
      keyword:'',
      nosearch:false,
      height:0,

      pagenum: 1,
      isfinishload:true,
      isfullresult:false,
    }
  },
  mounted: function() {
//    console.log(this.$route.query.isfreenum)
    if(this.$route.query.keyword!=null) {
      this.keyword=this.$route.query.keyword
    }
    if(this.$route.query.isfreenum!=null) {
      this.isfreenum=this.$route.query.isfreenum
    }
    if(this.$route.query.categoryid!=null) {
      this.categoryid=this.$route.query.categoryid
    }
    this.getData()

    let _this = this;
    window.onscroll = function(){
      //变量scrollTop是滚动条滚动时，距离顶部的距离
      var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
      //变量windowHeight是可视区的高度
      var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
      // console.log("scrollTop"+scrollTop)
      // console.log("windowHeight"+windowHeight)
      // console.log("scrollHeight"+scrollHeight)

      //滚动条到底部的条件
      if(scrollTop+windowHeight+200 > scrollHeight){
        //到了这个就可以进行业务逻辑加载后台数据了
        // _this.footerText = '我是有底线的';
        // console.log("到了底部");
        // _this.article.push(_this.article[0])
        if(_this.isfullresult==false & _this.isfinishload==true){
          _this.pagenum++
          _this.isfinishload=false
          _this.axios.get("api/search",{
            params: {
              categoryid:_this.categoryid,
              isfreenum:_this.isfreenum,
              keyword:_this.keyword,
              pagenum:_this.pagenum,
            }
          })
            .then(response =>{

              _this.article=_this.article.concat(response.data.article)
              // console.log(_this.article)
              _this.isfinishload=true

              // console.log(response.data.article.length)
              if(response.data.article.length<10){
                _this.isfullresult=true
                _this.isfinishload=true
              }
            })
            .catch(function (error) {
              // console.log("error")
            })
        }

      }

      console.log(_this.isfullresult)
  }

  },
  methods:{
    getData:function(){
      this.axios.get("api/search",{
        params: {
          categoryid:this.categoryid,
          isfreenum:this.isfreenum,
          keyword:this.keyword,
        }
      })
        .then(response =>{
          this.article = response.data.article
//          console.log(this.article.length)
          this.category = response.data.category

          if(this.article.length==0){
            this.nosearch=true
          }else {
            this.nosearch=false
          }

          if(this.article.length<10) {
            this.isfullresult=true
          }

          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
          }
        })
        .catch(function (error) {

        })
    },
    switchfilterclassUI:function(){
      this.filterclassUI = !this.filterclassUI
    },
    switchfiltercategoryUI:function(){
      this.filtercategoryUI = !this.filtercategoryUI
    },
    getcategoryname: function () {
      return this.category[this.categoryid-1].name
    },
    getisfree: function () {
      var isfreename=""

      if(this.isfreenum==0){
        isfreename="全部课程"
      }else if(this.isfreenum==1){
        isfreename="免费课程"
      }else{
        isfreename="vip课程"
      }

      return isfreename
    },
    submit: function () {
//      console.log(this.keyword)
      if(this.keyword==''){
        this.$router.push({
          name: 'Search',
          query: {

          }
        })
      }else{
        this.$router.push({
          name: 'Search',
          query: {
            keyword: this.keyword
          }
        })
      }
      this.categoryid=1
      this.isfreenum=0
      this.getData();
    },
    filterclassnum: function (isfreenum) {
      this.$router.push({
        name: 'Search',
        query: {
          isfreenum: isfreenum
        }
      })
      this.isfreenum=isfreenum
      this.categoryid=1
      this.keyword=''
      //console.log(this.isfreenum)
      this.getData()
      this.switchfilterclassUI()
    },
    filtercategorynum: function (categorynum) {
      this.$router.push({
        name: 'Search',
        query: {
          categoryid: categorynum
        }
      })
      this.categoryid=categorynum
      this.isfreenum=0
      this.keyword=''
      console.log(this.categoryid)
      this.getData()
      this.switchfiltercategoryUI()
    },
    toarticle: function (articleid) {
      this.$router.push({
        name: 'Article',
        query: {
          articleid: articleid
        }
      })
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    infinite: function(done) {
      // $(".pull-to-refresh-layer").hide();
      // console.log("infinite");
      this.page++;
      // console.log(this.page);
      //console.log(this.totalPages);
      if (this.page > this.totalPages) {
        this.page = this.totalPages;
        this.$refs.my_scroller.finishInfinite(true);
      } else {
        this.article.push(this.article[0])
        this.$refs.my_scroller.finishInfinite(false);
      }
    },

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../assets/css/swiper.css';
</style>



