<template>
  <div id='index' class="page-container">
    <!-- 顶部个人中心 -->
    <div id='topnav'>
      <div class="topnavfix">
        <div class="toplogin">
          <a @click="gotoLogin()" v-if='loginType==false' class="logintext">登录</a>
          <a @click="gotomine()" v-else class="logintext"><img src="/static/icon/icon-tomine.png"></a>
        </div>

        <div class="topsearch">
          <form @submit.prevent="submit($event)" class="topsearchform">
            <div class="topsearchbox">
              <div class="topsearchmain">
                <span class="topsearchicon">
                  <img src="/static/icon/icon-search.png" alt="" style="width:13px;">
                </span>
                <input type="search" name="keyword" placeholder="搜索您感兴趣的课程" class="topsearchinput" v-model="keyword">
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>

    <!-- 首页幻灯 -->
    <div id="banner">

      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide">
<!--            <img src="/static/image/banner00.png" alt="" style="width:100%;" @click="gotovip()">-->
            <img src="https://cdn.jsdelivr.net/gh/tlx159/imgholder@master/image/banner00.png" alt="" style="width:100%;" @click="gotovip()" >
          </div>
          <div class="swiper-slide">
<!--            <img src="/static/image/banner01.jpg" alt="" style="width:100%;" @click="toarticle(27)">-->
            <img src="https://cdn.jsdelivr.net/gh/tlx159/imgholder@master/image/banner02.png" alt="" style="width:100%;" @click="gotovip()">
          </div>
          <!--<div class="swiper-slide">-->
            <!--<img src="/static/image/banner3.jpg" alt="" style="width:100%;">-->
          <!--</div>-->

        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination" ></div>
      </div>

    </div>

    <!-- 首页功能导航 -->
    <div class="nav-btn">
      <div class="ui container">
        <div class="ui five column grid">
          <div class="column">
            <a @click='tosearchisfreenum(1)'>
              <img src="/static/icon/index-mianfei.png" alt="免费课程">
              <p>免费课程</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(2)'>
              <img src="/static/icon/index-zhuanqianxiangmu.png" alt="赚钱项目">
              <p>赚钱项目</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(3)'>
              <img src="/static/icon/index-baofen.png" alt="引流爆粉">
              <p>引流爆粉</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(4)'>
              <img src="/static/icon/index-douyinkuaishou.png" alt="抖音快手">
              <p>抖音快手</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(5)'>
              <img src="/static/icon/index-dianputaobao.png" alt="淘宝电商">
              <p>淘宝电商</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(6)'>
              <img src="/static/icon/index-dingjianwenan.png" alt="顶尖文案">
              <p>顶尖文案</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(7)'>
              <img src="/static/icon/index-seo.png" alt="SEO">
              <p>SEO</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(8)'>
              <img src="/static/icon/index-baidu.jpg" alt="百度系列">
              <p>百度系列</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(9)'>
              <img src="/static/icon/index-shequnyingxiao.png" alt="社群营销">
              <p>社群营销</p>
            </a>
          </div>
          <div class="column">
            <a @click='tosearchcategoryid(1)'>
              <img src="/static/icon/index-quanbufenlei.png" alt="全部分类">
              <p>全部分类</p>
            </a>
          </div>
        </div>
      </div>
    </div>


    <!-- 首页的文章列表 -->
    <div class="article-list">
      <div class="ui container">
        <div class="list-title">
          <div class="list-title-img">
            <img src="/static/icon/index-newlesson.png">
          </div>
          <div class="list-title-text">
            最新更新
          </div>
        </div>

        <a v-for='item in article' @click='toarticle(item.id)' class="articlecontent">
          <div class="articleleft">
            <div class="articleleftbox">
              <div class="articleleftimg">
                <!--                <img :src="'/static'+item.img" alt="">-->
                <img :src="'https://cdn.jsdelivr.net/gh/tlx159/imgholder@master'+item.img" alt="">-->
              </div>
              <div class="articleleftlearned">
                {{item.people}}人已学习
              </div>
            </div>
          </div>
          <div class="articleright">
            <div>
              <div class="articlerighttitle">
                {{item.title}}
              </div>
              <!-- <div class="articlerightinfo">
                在线观看下载（所有VIP可看）
              </div> -->
            </div>
            <div class="articlerightbottom">
              <span class="articlerightbottomtime">
                {{item.createtime}}发布
              </span>

              <span v-if='item.isFree==1' class="articlerightbottompricefree">
                免费
              </span>
              <span v-if='item.isFree==2' class="articlerightbottompricevip">
                付费
              </span>

            </div>
          </div>
        </a>

      </div>
    </div>


  </div>

</template>

<script>
  import Swiper from "swiper"

export default {
  name: 'Index',
  data () {
    return {
      article:[],
      userUI:false,
      loginType:false,
      username:'',
      password:'',
      keyword:''
    }
  },
  mounted: function() {
    this.getData();
    new Swiper('.swiper-container', {
      spaceBetween: 30,
      centeredSlides: true,
      autoplay: true,
      autoplay: {delay: 6000,},
      speed:600,
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
    })

  },
  methods:{
    getData:function(){
      this.axios.get("api/index")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
          }
//          console.log(response.data)
          this.article = response.data.article
        })
        .catch(function (error) {

        })
    },
    tosearchisfreenum:function(isfreenum){
      this.$router.push({
        name: 'Search',
        query: {
          isfreenum: isfreenum
        }
      })
    },
    tosearchcategoryid:function(categoryid){
      this.$router.push({
        name: 'Search',
        query: {
          categoryid: categoryid
        }
      })
    },
    submit: function () {
      this.$router.push({
        name: 'Search',
        query: {
          keyword: this.keyword
        }
      })
    },
    toarticle: function (articleid) {
      this.$router.push({
        name: 'Article',
        query: {
          articleid: articleid
        }
      })
    },
    gotovip: function () {
      this.$router.push({
        name: 'Vip',
        query: {

        }
      })
    },
    gotorumen: function () {
      this.$router.push({
        name: 'Article',
        query: {
          articleid:16
        }
      })
    },
    gotomine: function () {
      this.$router.push({
        name: 'Mine',
      })
    },
    gotoLogin: function () {
      this.$router.push({
        name: 'Login',
      })
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../assets/css/swiper.css';
</style>



