<template>
  <div id='Login' class="page-container">
    <div class="logintopbox">
      <a class="backtoindex" @click="gotoIndex()">
        <img src="/static/icon/icon-home.png">
        </a>
    </div>

    <div class="logincontainer">
      <div class="myclassicon">
        <img src="/static/image/cclogo.jpg">
      </div>

      <div class="loginbox">
        <div class="logingroup">
          <div class="loginname">
            <div class="loginicon">
              <img src="/static/icon/icon-email.png">
            </div>
            <input type="text" name="username" v-model='username' placeholder="请输入邮箱">
          </div>

          <div class="loginpw">
            <div class="loginicon">
              <img src="/static/icon/icon-pw.png">
            </div>
            <input type="password" name="password" v-model='password' placeholder="密码">
            <div class="forgetpw">
              <a @click="gotoForgetpw()">忘记密码</a>
            </div>
          </div>

          <div class="loginpw logincode" v-if='isCode==true'>
            <div class="loginicon">
              <img src="/static/icon/icon-code.png">
            </div>
            <input type="text" name="logincode" v-model='logincode' placeholder="验证码">
            <div class="forgetpw logincodebox">
              <img @click='userLogin("0")' :src="'data:image/png;base64,'+image_64">
            </div>
          </div>
        </div>
      </div>

      <div class="submitbox">
        <button @click='userLogin("1")' class="submitbtn" name="submitbtn">登录</button>
      </div>

      <div class="regbox">
        <a class="regbtn" @click="gotoRegister()">注册</a>
      </div>
    </div>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

  </div>
</template>

<script>
  import qs from 'qs'

export default {
  name: 'Login',
  data () {
    return {
      loginType:false,
      username:'',
      password:'',
      logincode:'',
      isCode:false,
      toastShow: false,
      toastText: '',
      image_64:'',
      articleid:'',
    }
  },
  mounted: function() {
    if(this.$route.params.articleid!=null) {
      this.articleid=this.$route.params.articleid
//      console.log(this.articleid)
    }
    this.getData();
  },
  methods: {
    getData: function () {
      this.axios.get("/api/login")
        .then(response => {
          if (response.data.loginType == 'ok') {
            this.loginType = true
            this.$router.push({
              name: 'Index',
            })
          } else {
            this.loginType = false
          }
//          console.log(response.data)
        })
        .catch(function (error) {

        })
    },
    userLogin: function (isFresh) {
      var myemailreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      var tempcode = ''
      if(this.isCode==false){
        tempcode='0'
      }else if(isFresh=="0"){
        tempcode='0'
      }else{
        tempcode=this.logincode
      }
      if (this.username == '') {
        this.toast("请输入邮箱")
      } else if (!myemailreg.test(this.username)) {
        this.toast("请输入有效邮箱")
      } else if (this.password == '') {
        this.toast("请输入密码")
      } else if (isFresh == "1" & this.isCode == true & this.logincode == '') {
        this.toast("请输入验证码")
      } else {
        this.axios.post("api/login",
          qs.stringify({
            username: this.username,
            password: this.password,
            logincode: tempcode
          })
        )
          .then(response => {
            console.log(response.data)
            if (response.data.loginType == 'success') {
              if (this.articleid!=''){
                this.$router.push({
                  name: 'Article',
                  query: {
                    articleid: this.articleid
                  }
                })
              }else {
                this.$router.push({
                  name: 'Index',
                })
              }
            } else if (response.data.loginType == 'wrongpw') {
              this.toast("密码错误")
              if (this.isCode == true) {
                this.image_64 = response.data.image_64
              }
            } else if (response.data.loginType == 'wronguser') {
              this.toast("邮箱未注册")
            } else if (this.isCode == false) {
              this.isCode = true
              this.image_64 = response.data.image_64
            } else {
              if (isFresh == "1") {
                this.toast("验证码错误")
              }
              this.isCode = true
              this.image_64 = response.data.image_64
            }
          })
          .catch(function (error) {
          })
      }
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    gotoForgetpw: function () {
      this.$router.push({
        name: 'Forgetpw',
      })
    },
    gotoRegister: function () {
      this.$router.push({
        name: 'Register',
      })
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



