          </div>
          <div v-else-if="viplevel<5 && orderlimit==true" @click="showvip()" class="buyvip buyvipgly">
            <a>联系管理员</a>
          </div>
          <div v-else="" class="hasvip">
            已开通
          </div>
        </li>

        <li v-if="viplevel<7">
          <div class="vipicon">
            <img src="/static/icon/vip-vipicon5.png" alt="">
          </div>
          <div class="vipnotice">
