      </ul>
    </div>

    <!--付款弹窗-->
    <div v-show="showpaybox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closepaybox()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertclass">您将要开通的是{{getpaytype()}}，￥{{getpayprice()}}</p>
          <p class="payboxalertdes">请使用{{getpayclasstype()}}扫码支付</p>
          <p class="payboxalertadd">请在{{timecount}}秒内完成支付，成功后5S内开通VIP</p>
          <div class="payboximg">
            <img :src="image_64" alt="付款二维码">
            <!--<img src="/static/image/vip-loading.png" alt="付款二维码">-->
          </div>
        </div>

      </div>
    </div>

    <!--客服弹窗-->
    <div v-show="showvipbox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closevip()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertvip">请扫描下方二维码添加老师微信</p>
          <!--<p class="payboxalertdes">请使用{{getpayclasstype()}}扫码支付</p>-->
          <!--<p class="payboxalertadd">请在{{timecount}}秒内完成支付，成功后5S内开通VIP</p>-->
          <div class="payboximg">
            <img src="/static/image/kefuweixin.jpg" alt="付款二维码">
            <!--<img src="/static/image/vip-loading.png" alt="付款二维码">-->
          </div>
        </div>

      </div>
    </div>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

  </div>




</template>

<script>

export default {
  name: 'vip',
  data () {
    return {
      loginType:false,
      viplevel:1,
      outtime:'',
      showpaybox:false,
      paytype:0,
      payclasstype:0,
      image_64:'',
      buyvipflag:true,
      toastShow: false,
      toastText: '',
      timecount:'',
      timer:null,
      aid:'',
      free_limit:false,
      orderlimit:false,
      showvipbox:false,
    }
  },
  mounted: function() {
    this.getData();

  },
  methods:{
    getData:function(){
      this.axios.get("api/vip")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Login',
            })
          }
         console.log(response.data)
          this.viplevel=response.data.viplevel
          if(response.data.orderlimit=='yes'){
            this.orderlimit=true
          }else {
            this.orderlimit=false
          }
          if(this.viplevel==2){
            this.outtime=response.data.outtime
//            console.log(this.outtime.indexOf("T"))
            this.outtime=this.outtime.substring(0,this.outtime.indexOf("T"))
//            console.log(this.outtime)
          }
        })
        .catch(function (error) {

        })
    },
    buyvip:function(paytype,classnum){
      this.buyvipflag=false
      this.paytype=paytype
      this.payclasstype=classnum
      this.axios.get("api/order",{
        params: {
          paytype:paytype,
          classnum:classnum,
        }
      })
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Login',
            })
          }
          console.log(response.data)
          if(response.data.orderstatus == 'fourmin'){
            this.toast("操作过于频繁，请4分钟后再试")
            this.buyvipflag=true
          }else if(response.data.orderstatus=='ok') {
            this.showpaybox=true
            this.image_64=response.data.image_64
            this.aoid=response.data.aoid

            const TIME_COUNT = 234
            if (!this.timer) {
              this.timecount = TIME_COUNT;
              this.timer = setInterval(() => {
                if (this.timecount > 0 && this.timecount <= TIME_COUNT) {
                  this.timecount--
                  if(this.timecount%5==0){
                    this.axios.get("api/queryorder",{
                      params: {
                        aoid:this.aoid,
                      }
                    })
                      .then(response =>{
                          if(response.data.status=='success'){
                            this.getData();
                            this.showpaybox=false
                            this.buyvipflag=true
                            this.image_64=''
                            this.toast("支付成功")
                            clearInterval(this.timer)
                            this.timer=null
                            this.timecount=0
                          }else if(response.data.status=='new'){
                            console.log("new")
                          }else {
                            this.showpaybox=false
                            this.buyvipflag=true
                            this.image_64=''
                            clearInterval(this.timer)
                            this.timer=null
                            this.timecount=0
                          }
                      })
                      .catch(function (error) {

                      })
                  }
                } else {
                  this.showpaybox=false
                  this.buyvipflag=true
                  clearInterval(this.timer)
                  this.timer=null
                  this.timecount=0
                }
              }, 1000)
            }
          }else{
            this.toast("收款已达上限，请联系管理员手动开通")
          }

        })
        .catch(function (error) {

        })
    },
    closepaybox:function(){
      this.showpaybox=false
      this.buyvipflag=true
    },
    getpaytype:function () {
      if(this.payclasstype==2){
        return "年费VIP"
      }else if(this.payclasstype==3){
        return "终身VIP"
      }else if(this.payclasstype==4){
        return "合伙人"
      }else{
        return "超级合伙人"
      }
    },
    getpayprice:function () {
      if(this.payclasstype==2){
        return "99"
      }else if(this.payclasstype==3){
        return "199"
      }else if(this.payclasstype==4){
        return "399"
      }else{
        return "599"
      }
    },
    getpayclasstype:function () {
      if(this.paytype==1){
        return "微信"
      }else {
        return "支付宝"
      }
    },
    showvip:function(){
      this.showvipbox=true
    },
    closevip:function(){
      this.showvipbox=false
    },
    goBack:function(){
      this.$router.go(-1)
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  }

}
</script>

