  <div id='Article' class="page-container">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a class="mine" @click="goBack()"></a>
      <div class="submineheadtitle">课程详情</div>
    </div>

    <!-- 顶部个人中心 -->
    <div class="articletopimg" v-for='item in article'>
<!--      <img v-show="classimgshow" style='max-width: 640px; max-height: 385px; width: 100vw; height: 60vw;' :src="'/static'+item.img">-->
      <img v-show="classimgshow" style='max-width: 640px; max-height: 385px; width: 100vw; height: 60vw;' :src="'https://cdn.jsdelivr.net/gh/tlx159/imgholder@master'+item.img">
      <!--<div v-show="classshow" id="vs"></div>-->
      <!--<iframe v-show="classshow" style='max-width: 640px; max-height: 385px; width: 100vw; height: 60vw;' frameborder='no' allowfullscreen mozallowfullscreen webkitallowfullscreen :src="'http://share.polyv.net/front/video/preview?vid='+currentclasssrc"></iframe>-->
