    <!--西瓜视频播放器-->
<!--    <div v-show="classshow" style='max-width: 640px; max-height: 385px; width: 100vw;' id="vs"></div>-->
    <!--<div v-for="(item,key) in classsrc" v-show="currentclassnum==key" style='max-width: 640px; max-height: 385px; width: 100vw;' :id="'vs'+key"></div>-->

    <!--酷播云-->
    <!--<iframe style='width: 100%;' frameborder='no' allowfullscreen mozallowfullscreen webkitallowfullscreen src='http://share.polyv.net/front/video/preview?vid=3b8c060372b8c0b9cc160e2c78fdb2cb_3'></iframe>-->

    <!-- 简介和目录tab -->
    <div class="filterbox filterboxarticle">
      <div class="ui container">
        <div class="ui two column grid">
          <div @click='showbrief()' class="column">
            <div class="articletab " :class="{'articletabactive':articlebrief==true}">课程简介</div>
          </div>
          <div class="column">
            <div class="articletab" v-if="loginType=='no'" @click='gotologin()'  :class="{'articletabactive':articlebrief==false}">立即登录</div>
            <div class="articletab" v-else-if="isvip==true" @click='showdetail()'  :class="{'articletabactive':articlebrief==false}">课程目录</div>
            <div class="articletab" v-else @click='gotovip()' :class="{'articletabactive':articlebrief==false}">开通VIP</div>
          </div>
        </div>
      </div>
    </div>

    <!-- 课程简介 -->
    <div v-show='articlebrief' class="articlebrief" v-for='item in article'>
      <div class="articlebrieftop">
        <div class="articlebrieftitle">
          {{item.title}}
        </div>
        <div class="articlebriefpeople">
          {{item.people}}人已学习
        </div>
        <div class="articlebriefprice">
          <!-- <div class="articlebriefisfree">
            免费
          </div> -->
          <div v-if='item.isFree==1' class="articlebriefpricefree">
            免费
          </div>
          <div v-if='item.isFree==2' class="articlebriefpricevip">
            VIP
          </div>
          <div class="articlebriefcollect" @click='switchcollect()'>
            <span class="articlebrieficon" :class="{'cur':iscollect==true}"></span>
            <!-- <span class="articlebrieficon cur"></span> -->
            {{classsrc1}}
          </div>
        </div>

      </div>

      <div class="articlebriefmiddle"></div>

      <div v-html="item.content" class="articlebriefbottom">

      </div>

    </div>

    <!-- 课程目录 -->
    <!-- 视频版 -->
<!--    <div v-show='articledetail' class="articledetail">-->
<!--      <ul class="articledetaillist">-->
<!--        <li v-for='(item,key) in classsrc' @click="watchclass(key)" class="articledetailitem" :class="{'cur':currentclassnum==key}">-->
<!--          <div class="articledetailbox">-->
<!--            <div class="articledetailleft">-->
<!--              视频-->
<!--            </div>-->
<!--            <p class="articledetailright">-->
<!--              {{getclassname(key)}}-->
<!--            </p>-->
<!--          </div>-->
<!--        </li>-->
<!--        &lt;!&ndash;<li @click="watchclass(2)" class="articledetailitem" :class="{'cur':currentclassnum==2}">&ndash;&gt;-->
<!--          &lt;!&ndash;<div class="articledetailbox">&ndash;&gt;-->
<!--            &lt;!&ndash;<div class="articledetailleft">&ndash;&gt;-->
<!--              &lt;!&ndash;视频&ndash;&gt;-->
<!--            &lt;!&ndash;</div>&ndash;&gt;-->
<!--            &lt;!&ndash;<p class="articledetailright">&ndash;&gt;-->
<!--              &lt;!&ndash;2.产品经理方法论&ndash;&gt;-->
<!--            &lt;!&ndash;</p>&ndash;&gt;-->
<!--          &lt;!&ndash;</div>&ndash;&gt;-->
<!--        &lt;!&ndash;</li>&ndash;&gt;-->
<!--      </ul>-->
<!--    </div>-->
    <!-- 网盘版 -->
    <div v-show='articledetail' class="articledetail">
      <ul class="articledetaillist">
        <li>
          <div class="articledetailbox">
<!--            <div class="articledetailleft">-->
<!--              视频-->
<!--            </div>-->
            <p v-if="classshow==true" class="wptext">
              {{getclassname(0)}}
            </p>
          </div>
        </li>
      </ul>
<!--      <h5>链接：https://pan.baidu.com/s/1hboWnCoZvaDbz9XVk3u96A 提取码：i571</h5>-->
    </div>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

  </div>
</template>

<script>
  import qs from 'qs'
  import Player from 'xgplayer'

export default {
  name: 'Article',
  metaInfo() {
    return {
      title: this.title,
      meta: this.metaData
    }
  },

  data () {
    return {
      article:[],
      articleid:0,
      articlebrief:true,
      articledetail:false,
      iscollect:false,
      toastShow: false,
      toastText: '',
      currentclassnum:-1,
      classimgshow:true,
      classsrc:[],
      classsrc1:'',
      currentclasssrc:'',
      classshow:false,
      rlen:history.length,
      player:'',
      isvip:false,
      loginType:'no',
      title:'',

      metaData:[{
        name: 'description',
        content: ''
      }],
    }
  },
  mounted: function() {
    if(this.$route.query.articleid!=null) {
      this.articleid=this.$route.query.articleid
    }else {
      this.$router.push({
        name: 'Index',
      })
    }

    this.getData()

//    西瓜视频播放器
//    const player = new Player({
//      id: 'vs',
////      url: 'http://s2.pstatp.com/cdn/expire-1-M/byted-player-videos/1.0.0/xgplayer-demo.mp4',
////      url:'/static/video/sjfx/sjfx1.mp4',
//      url:'',
//      playbackRate: [0.5, 0.75, 1, 1.5, 2],
//      fluid: true
//    })
      this.player = new Player({
        id: 'vs',
        url:'/static/video/sjfx/sjfx1.mp4',
        playbackRate: [0.5, 0.75, 1, 1.5, 2],
        fluid: true
      })

  },
  methods: {
    getData: function () {
      this.axios.get("/api/article",{
        params: {
          articleid:this.articleid,
        }
      })
        .then(response => {
          if(response.data.loginType!='no'){
            this.loginType='yes'
            if(response.data.isarticle!='no'){
              if(response.data.iscollect=='yes'){
                this.iscollect=true
              }
              response.data.article[0].content=response.data.article[0].content.replace("/upload","/static/upload")
              response.data.article[0].content=response.data.article[0].content.replace(/height/g,"")
             // console.log(response.data.article)
              this.article = response.data.article
              this.title=this.article[0].title
              this.metaData = [{
                name: 'description',
                content: this.article[0].content.replace(/<.+?>/g, '').replace(/&nbsp;/ig, '')
              }]

              // this.classsrc=response.data.classsrc
              if(response.data.isvip=='no'){
                this.isvip=false
              }else {
                this.isvip=true
              }
            }
            else{
              this.$router.push({
                name: 'Index',
              })
            }
          }
          else{
            response.data.article[0].content=response.data.article[0].content.replace("/upload","/static/upload")
            response.data.article[0].content=response.data.article[0].content.replace(/height/g,"")
            this.article = response.data.article
            console.log(this.article)
            this.title=this.article[0].title
            this.metaData = [{
              name: 'description',
              content: this.article[0].content.replace(/<.+?>/g, '').replace(/&nbsp;/ig, '')
            }]

            // console.log(this.metaData)
            // this.$router.push({
            //   name: 'Login',
            //   params: {
            //     articleid: this.articleid
            //   }
            // })
          }
        })
        .catch(function (error) {

        })
    },
    showbrief: function () {
      this.articlebrief=true
      this.articledetail=false
    },
    showdetail: function () {
      this.axios.get("/api/classsrc",{
        params: {
          articleid:this.articleid,
        }
      })
        .then(response => {
          if(response.data.loginType!='no'){
            if(response.data.isvip!='no'){
              if(response.data.learntoday!='no'){
                this.classshow=true
                this.classsrc=response.data.classsrc
                //console.log(response.data.classsrc[0])
              }else {
                this.toast("今天已学习5门课程，请明天再试")
                this.showbrief()
              }
            }else {
              this.$router.push({
                name: 'Index',
              })
            }
          }
          else{
            this.$router.push({
              name: 'Login',
            })
          }
        })
        .catch(function (error) {

        })
      this.articlebrief=false
      this.articledetail=true
    },
    getiscollect: function () {
      if(this.iscollect==true){
        return "已收藏"
      }else{
        return "收藏"
      }
    },
    switchcollect: function () {
      console.log(this.loginType)
      if(this.loginType!='no') {
        this.axios.post("api/switchcollect",
          qs.stringify({
            articleid: this.articleid,
          })
        )
          .then(response => {
            if (response.data.switchcollect == 'success') {
              if (this.iscollect == true) {
                this.toast("已取消收藏")
              } else {
                this.toast("已收藏该课程")
              }
              this.iscollect = !this.iscollect
            } else {
              //            console.log(response.data.switchcollect)
              this.toast("请求错误")
            }
          })
          .catch(function (error) {
          })
      }else{
        this.toast("请登录后收藏")
      }
    },
    watchclass:function(classnum) {
      this.currentclassnum=classnum
      this.currentclasssrc=this.classsrc[this.currentclassnum].classsrc
//      console.log(this.player)
//      this.player.pause()
//      this.player.destroy()
//      this.player = new Player({
//        id: 'vs',
//        url:this.currentclasssrc,
//        playbackRate: [0.5, 0.75, 1, 1.5, 2],
//        fluid: true
//      })
      this.player.start(this.currentclasssrc)
      this.player.play()
      this.classimgshow=false
      this.classshow=true
    },
    getclassname:function(key) {
      return this.classsrc[key].classname
    },
//    showclass:function(classnum) {
//      if(classnum==this.currentclassnum){
//        this.classimgshow=false
//        return true
//      }
//    },
    goBack:function(){
      let len = this.rlen - history.length - 1
      this.$router.go(len)
    },
    gotovip: function () {
      this.$router.push({
        name: 'Vip',
        query: {

        }
      })
    },
    gotologin: function () {
      this.$router.push({
        name: 'Login',
        query: {

        }
      })
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  },
}
</script>

