<template>
  <div id='Forgetpw' class="page-container">
    <a class="backtoindex" @click="gotoIndex()">
      <img src="/static/icon/icon-home.png">
      </a>

        <div class="logincontainer">

          <div class="loginbox">
            <div class="forgetpwinputemail">请输入注册时的邮箱,以收取验证码</div>
            <div class="logingroup">
              <div class="loginname">
                <div class="loginicon">
                  <img src="/static/icon/icon-email.png">
                </div>
                <input type="text" name="username" v-model='username' placeholder="请输入邮箱">
              </div>
            </div>
          </div>

          <div class="submitbox">
            <button @click='sendEmail()' :disabled="isSend==true" class="submitbtn" name="submitbtn">下一步</button>
          </div>


          <div class="regnavtext">
            <a @click="gotoLogin()">立即登录</a>
            <span>|</span>
            <a @click="gotoRegister()">注册新账号</a>
          </div>
        </div>

        <div class="toast" v-show="toastShow">
          <div class="toastbox">
            <div class="toasticonbox">
              <img src="/static/icon/icon-toast.png">
            </div>
            {{toastText}}
          </div>
        </div>

  </div>

</template>

<script>
  import qs from 'qs'

export default {
  name: 'Forgetpw',
  data () {
    return {
      loginType:false,
      username:'',
      toastShow: false,
      toastText: '',
      isSend:false,
    }
  },
  mounted: function() {

    this.getData();
  },
  methods: {
    getData: function () {
      this.axios.get("/api/forgetpw")
        .then(response => {
          if (response.data.loginType == 'ok') {
            this.loginType = true
            this.$router.push({
              name: 'Index',
            })
          } else {
            this.loginType = false
          }
//          console.log(response.data)
        })
        .catch(function (error) {

        })
    },
    sendEmail: function () {
      this.isSend=true;
      var myemailreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      if(this.username==''){
        this.toast("请输入邮箱")
      }else if(!myemailreg.test(this.username)){
        this.toast("请输入有效邮箱")
      }else{
        this.isSend=false;
        this.axios.post("api/forgetpw",
          qs.stringify({
            username:this.username,
          })
        )
          .then(response => {
//            console.log(response.data)
            if (response.data.sendType=='success') {
              this.toast("已发送邮件")
              this.$router.push({
                name: 'Changepw',
              })
            }else if (response.data.sendType=='nouser'){
              this.toast("该邮箱未注册")
            }else{
              this.toast("参数非法")
            }
          })
          .catch(function (error) {
          })
      }
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    gotoLogin: function () {
      this.$router.push({
        name: 'Login',
      })
    },
    gotoRegister: function () {
      this.$router.push({
        name: 'Register',
      })
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



