<template>
  <div id='poster' class="page-container">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a @click="gotoRetail()"></a>
      <div class="submineheadtitle">推广海报</div>
    </div>

    <div class="posterqrcode">
      <img :src="'data:image/jpg;base64,'+image_64" alt="推广海报生成中，请稍等10s左右">
    </div>

  </div>






</template>

<script>

export default {
  name: 'poster',
  data () {
    return {
      image_64:'',
    }
  },
  mounted: function() {
    this.getData();

  },
  methods:{
    getData:function(){
      this.axios.get("api/poster")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }

          this.image_64=response.data.image_64
        })
        .catch(function (error) {

        })
    },
    gotoRetail: function () {
      this.$router.push({
        name: 'Retail',
      })
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



