<template>
  <div id="User">
    {{ page_info }}
    <hr>
    {{ djaogo_message }}
  </div>
</template>

<script>
export default {
  name: 'User',
  data () {
    return {
      page_info: 'this is the User page',
      djaogo_message:""
    }
  },
  created(){
    this.axios.get("api/test/")
      .then(response =>{
        console.log(response.data)
        this.djaogo_message=response.data.message
      })
      .catch(function (error) {
        console.log(error)
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



