<template>
  <div id='money' class="page-container">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a @click="gotoRetail()"></a>
      <div class="submineheadtitle">取现记录</div>
    </div>
    <div v-if="nomoney==false">
      <div class="teamhead">
        <div class="teamheadinfo">取现日期</div>
        <div class="moneyheadnum">取现金额</div>
      </div>

      <div v-for='item in money' class="teamlist">
        <div class="teambox">
          <div class="moneytime">{{getDate(item.moneytime)}}</div>
          <div class="teamlistnum">{{item.money}}</div>
        </div>
      </div>
    </div>

    <!-- 没有搜索结果 -->
    <div v-else="" class="nosearchbox">
      <div class="norecordbd">
        <h3>目前暂无提现记录~</h3>
      </div>
    </div>

  </div>






</template>

<script>

export default {
  name: 'money',
  data () {
    return {
      loginType:true,
      money:[],
      nomoney:false,
    }
  },
  mounted: function() {
    this.getData();

  },
  methods:{
    getData:function(){
      this.axios.get("api/money")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }
          if(response.data.money=='no'){
            this.nomoney=true
          }else {
            this.nomoney=false
            this.money=response.data.moneydata
          }

        })
        .catch(function (error) {

        })
    },
    getDate:function(time) {
      return time.slice(0,time.indexOf('T'))
    },
    gotoRetail: function () {
      this.$router.push({
        name: 'Retail',
      })
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



