<template>
  <div id='Changepw' class="page-container">
    <a class="backtoindex" @click="gotoIndex()">
      <img src="/static/icon/icon-home.png">
      </a>

        <div class="logincontainer">

          <div class="loginbox">
            <div class="forgetpwinputemail">已向您的邮箱发送一个随机验证码，请在下方输入</div>
            <div class="logingroup">
              <div class="loginname">
                <div class="loginicon">
                  <img src="/static/icon/icon-code.png">
                </div>
                <input type="text" name="code" v-model='code' placeholder="请输入验证码">
              </div>
              <div class="loginname">
                <div class="loginicon">
                  <img src="/static/icon/icon-pw.png">
                </div>
                <input type="password" name="password" v-model='password' placeholder="请输入密码（长度不小于9位）">
              </div>
              <div class="loginname">
                <div class="loginicon">
                  <img src="/static/icon/icon-pw.png">
                </div>
                <input type="password" name="confirmpw" v-model='confirmpw' placeholder="确认密码">
              </div>
            </div>
          </div>

          <div class="submitbox">
            <button @click='changePw()' class="submitbtn" name="submitbtn">修改密码</button>
          </div>


          <div class="regnavtext">
            <a @click="gotoLogin()">立即登录</a>
            <span>|</span>
            <a @click="gotoRegister()">注册新账号</a>
          </div>
        </div>

        <div class="toast" v-show="toastShow">
          <div class="toastbox">
            <div class="toasticonbox">
              <img src="/static/icon/icon-toast.png">
            </div>
            {{toastText}}
          </div>
        </div>

  </div>
</template>

<script>
  import qs from 'qs'

export default {
  name: 'Changepw',
  data () {
    return {
      loginType:false,
      code:'',
      password:'',
      confirmpw:'',
      toastShow: false,
      toastText: '',
    }
  },
  mounted: function() {
    this.getData();
  },
  methods: {
    getData: function () {
      this.axios.get("/api/changepw")
        .then(response => {
          if(response.data.loginType == 'ok'){
            this.loginType = true
            this.$router.push({
              name: 'Index',
            })
          }else if(response.data.changepwType == 'invalid'){
            this.$router.push({
              name: 'Forgetpw',
            })
          }
        })
        .catch(function (error) {

        })
    },
    changePw: function (isFresh) {
      if(this.code==''){
        this.toast("请输入验证码")
      }else if(this.password.length<9){
        this.toast("请输入长度不小于9位的密码")
      }else if(this.confirmpw==''){
        this.toast("请输入确认密码")
      }else if(this.password!=this.confirmpw){
        this.toast("两次密码不一致")
      }else {
        this.axios.post("api/changepw",
          qs.stringify({
            code:this.code,
            password:this.password,
          })
        )
          .then(response => {
            if (response.data.changepwType=='success') {
              this.toast("修改密码成功")
              this.$router.push({
                name: 'Login',
              })
            }else if (response.data.changepwType=='wrongcode'){
              this.toast("验证码错误")
            }else if (response.data.changepwType=='wrongtime'){
              this.toast("错误次数过多")
              this.$router.push({
                name: 'Login',
              })
            }else{
              this.toast("参数非法")
            }
          })
          .catch(function (error) {
          })
      }
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    gotoLogin: function () {
      this.$router.push({
        name: 'Login',
      })
    },
    gotoRegister: function () {
      this.$router.push({
        name: 'Register',
      })
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



