<template>
  <div id='Register' class="page-container">
    <a class="backtoindex" @click="gotoIndex()">
      <img src="/static/icon/icon-home.png">
      </a>

        <div class="logincontainer">
          <div class="myclassicon">
            <img src="/static/image/cclogo.jpg">
          </div>


          <div class="loginbox">
            <div class="logingroup">
              <div class="loginname">
                <div class="loginicon">
                  <img src="/static/icon/icon-email.png">
                </div>
                <input type="text" name="username" v-model='username' placeholder="请输入邮箱">
              </div>
              <div class="loginpw">
                <div class="loginicon">
                  <img src="/static/icon/icon-pw.png">
                </div>
                <input type="password" name="password" v-model='password' placeholder="请输入密码（长度不小于9位）">
              </div>
              <div class="loginpw confirmpw">
                <div class="loginicon">
                  <img src="/static/icon/icon-pw.png">
                </div>
                <input type="password" name="confirmpw" v-model='confirmpw' placeholder="确认密码">
              </div>
            </div>
          </div>

          <div class="submitbox">
            <button @click='userRegister()' class="submitbtn" name="submitbtn">注册</button>
          </div>


          <div class="regnavtext">
            <a @click="gotoLogin()">立即登录</a>
            <span>|</span>
            <a @click="gotoForgetpw()">找回密码</a>
          </div>

        <div class="toast" v-show="toastShow">
          <div class="toastbox">
            <div class="toasticonbox">
              <img src="/static/icon/icon-toast.png">
            </div>
            {{toastText}}
          </div>
        </div>
      </div>
  </div>
</template>

<script>
  import qs from 'qs'

export default {
  name: 'Register',
  data () {
    return {
      loginType:false,
      registerType:false,
      username:'',
      password:'',
      confirmpw:'',
      toastShow: false,
      toastText: '',
      userid:0,
    }
  },
  mounted: function() {
    if(this.$route.query.userid!=null) {
      this.userid=this.$route.query.userid
    }
    this.getData();
  },
  methods: {
    getData: function () {
      this.axios.get("/api/register")
        .then(response => {
          if (response.data.loginType == 'ok') {
            this.loginType = true
            this.$router.push({
              name: 'Index',
            })
          } else {
            this.loginType = false
          }
        })
        .catch(function (error) {

        })
    },
    userRegister: function () {
      var myemailreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
      if(this.username==''){
        this.toast("请输入邮箱")
      }else if(!myemailreg.test(this.username)){
        this.toast("请输入有效邮箱")
      }else if(this.password.length<9){
        this.toast("请输入长度不小于9位的密码")
      }else if(this.confirmpw==''){
        this.toast("请输入确认密码")
      }else if(this.password!=this.confirmpw){
        this.toast("两次密码不一致")
      }else {
        this.axios.post("api/register",
          qs.stringify({
            username:this.username,
            password:this.password,
            userid:this.userid
          })
        )
          .then(response => {
            console.log(response.data)
            if (response.data.registerType=='success') {
              this.toast("注册成功")
              this.$router.push({
                name: 'Login',
              })
            }else if(response.data.registerType=='hasuser') {
              this.toast("该邮箱已被注册")
            }else{
              this.toast("注册数据错误")
            }
          })
          .catch(function (error) {
          })
      }
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    gotoLogin: function () {
      this.$router.push({
        name: 'Login',
      })
    },
    gotoForgetpw: function () {
      this.$router.push({
        name: 'Forgetpw',
      })
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



