<template>
  <div id='Mine' class="page-container minebg">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a class="subminehome" @click="gotoIndex()"></a>
      <div class="submineheadtitle">个人中心</div>
    </div>

    <div class="minehead">

      <div class="minheadbody">
        <div class="minheadimg"><img src="/static/image/mineheaduser.png"></div>
        <div class="minheadviplevel">{{viplevelname}}</div>
        <div class="minheademail">{{username}}</div>
      </div>

    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="gotoCollection()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-collection.png">
        </div>
        <div class="mineunitsubtext">课程收藏</div>
      </a>
      <a class="mineunitsub" @click="gotoFoot()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-footprint.png">
        </div>
        <div class="mineunitsubtext">我的足迹</div>
      </a>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="gotoVip()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-VIP.png">
        </div>
        <div class="mineunitsubtext">VIP服务</div>
      </a>
      <a class="mineunitsub" @click="toretail()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-getmoney.png">
        </div>
        <div class="mineunitsubtext">课程推广</div>
      </a>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="showvip()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-customerser.png">
        </div>
        <div class="mineunitsubtext">课程咨询</div>
      </a>
    </div>

    <div class="minelogout">
      <button @click='logout()'>退出登录</button>
    </div>

    <footer>备案号：京ICP备20020041号</footer>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

    <!--客服弹窗-->
    <div v-show="showvipbox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closevip()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertvip">请扫描下方二维码添加老师微信</p>
          <!--<p class="payboxalertdes">请使用{{getpayclasstype()}}扫码支付</p>-->
          <!--<p class="payboxalertadd">请在{{timecount}}秒内完成支付，成功后5S内开通VIP</p>-->
          <div class="payboximg">
            <img src="/static/image/kefuweixin.jpg" alt="付款二维码">
            <!--<img src="/static/image/vip-loading.png" alt="付款二维码">-->
          </div>
        </div>

      </div>
    </div>

  </div>

</template>

<script>
  import qs from 'qs'
export default {
  name: 'Mine',
  data () {
    return {
      loginType:false,
      toastShow: false,
      toastText: '',
      username:'',
      viplevelname:'',
      showvipbox:false
    }
  },
  mounted: function() {
    this.getData();
  },
  methods:{
    getData:function(){
      this.axios.get("api/mine")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }
          this.username=response.data.username
          if(response.data.viplevel==1){
            this.viplevelname='普通用户'
          }else if(response.data.viplevel==2){
            this.viplevelname='年费VIP'
          }else if(response.data.viplevel==3){
            this.viplevelname='终身VIP'
          }else if(response.data.viplevel==4){
            this.viplevelname="合伙人"
          }else if(response.data.viplevel==5){
            this.viplevelname="超级合伙人"
          }else{
            this.viplevelname="加盟商"
          }
        })
        .catch(function (error) {

        })
    },
    logout: function () {

      this.axios.post("api/logout",
        qs.stringify({
        })
      )
        .then(response => {
          if (response.data.logoutType=='success') {
            this.toast("登出成功")
            this.$router.push({
              name: 'Index',
            })
          }
        })
        .catch(function (error) {
        })

    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
    gotoIndex: function () {
      this.$router.push({
        name: 'Index',
      })
    },
    gotoCollection: function () {
      this.$router.push({
        name: 'Collection',
      })
    },
    gotoFoot: function () {
      this.$router.push({
        name: 'Foot',
      })
    },
    gotoVip: function () {
      this.$router.push({
        name: 'Vip',
      })
    },
    showvip:function(){
      this.showvipbox=true
    },
    closevip:function(){
      this.showvipbox=false
    },
    toretail:function(){
      if(this.viplevelname=='普通用户'){
        this.toast("您现在是普通会员，暂时无法推广")
      }else if(this.viplevelname=='年费VIP'){
        this.toast("您现在是年费VIP，暂时无法推广")
      }else if(this.viplevelname=='加盟商'){
        this.toast("您现在是加盟商，请联系管理员开通独立系统")
      }else {
        this.$router.push({
          name: 'Retail',
        })
      }
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



