<template>
  <div id='foot' class="page-container">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a @click="gotoMine()"></a>
      <div class="submineheadtitle">我的足迹</div>
    </div>

    <!-- 文章列表 -->
    <div class="article-list-foot">
        <a v-for='item in article' @click='toarticle(item.id)' class="articlecontent">
          <div class="articleleft">
            <div class="articleleftbox">
              <div class="articleleftimg">
                <img :src="'/static'+item.img" alt="">
              </div>
              <div class="articleleftlearned">
                {{item.people}}人已学习
              </div>
            </div>
          </div>
          <div class="articleright">
            <div>
              <div class="articlerighttitle">
                {{item.title}}
              </div>
              <!-- <div class="articlerightinfo">
                在线观看下载（所有VIP可看）
              </div> -->
            </div>
            <div class="articlerightbottom">
              <span class="articlerightbottomtime">
                {{item.createtime}}发布
              </span>

              <span v-if='item.isFree==1' class="articlerightbottompricefree">
                免费
              </span>
              <span v-if='item.isFree==2' class="articlerightbottompricevip">
                VIP
              </span>

            </div>
          </div>
        </a>

      </div>

    <!--无结果-->
    <div v-show='noresult' class="nosearchbox">
      <div class="nosearchbd">
        <h3>没有浏览任何课程~</h3>
        <p>
          请先浏览看看我们的课程哦
        </p>
      </div>
    </div>

    </div>




</template>

<script>

export default {
  name: 'foot',
  data () {
    return {
      article:[],
      loginType:false,
      noresult:false,
    }
  },
  mounted: function() {
    this.getData();

  },
  methods:{
    getData:function(){
      this.axios.get("api/foot")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }
//          console.log(response.data)
          this.article = response.data.article
          if(this.article.length==0){
            this.noresult=true
          }
        })
        .catch(function (error) {

        })
    },
    gotoMine: function () {
      this.$router.push({
        name: 'Mine',
      })
    },
    toarticle: function (articleid) {
      this.$router.push({
        name: 'Article',
        query: {
          articleid: articleid
        }
      })
    },

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



