<template>
  <div id='team' class="page-container">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a @click="gotoRetail()"></a>
      <div class="submineheadtitle">我的团队</div>
    </div>

    <div  v-if="noteam==false">
      <div class="teamhead">
        <div class="teamheadinfo">成员信息</div>
        <div class="teamheadnum">Ta的佣金</div>
      </div>

      <div v-for='item in invite' class="teamlist">
        <div class="teambox">
          <div class="teamlistimg"><img src="/static/image/mineheaduser.png"></div>
          <div class="teamlistinfo">
            <div class="teamlistmemberinfo">
              <!--<span>id：1</span>-->
              <!--182377718@qq.com-->
              {{item.username}}
            </div>
            <span>{{getDate(item.createtime)}}</span>
          </div>
          <div class="teamlistnum">+{{getMoney(item.viplevel)}}</div>
        </div>
      </div>
    </div>

    <!-- 没有搜索结果 -->
    <div v-else="" class="nosearchbox">
      <div class="norecordbd">
        <h3>目前暂无邀请记录~</h3>
        <p>
        <a @click="gotoPoster()">生成海报立即去推广…</a>
        </p>
      </div>
    </div>

  </div>






</template>

<script>

export default {
  name: 'team',
  data () {
    return {
      loginType:true,
      invite:[],
      userlevel:0.3,
      noteam:false,
    }
  },
  mounted: function() {
    this.getData();

  },
  methods:{
    getData:function(){
      this.axios.get("api/invite")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }
          console.log(response.data)
          if(response.data.userlevel==1){
            this.$router.push({
              name: 'Index',
            })
          }else if(response.data.userlevel==2){
            this.$router.push({
              name: 'Index',
            })
          }else if(response.data.userlevel==3){
            this.userlevel=0.3
          }else if(response.data.userlevel==4){
            this.userlevel=0.5
          }else {
            this.userlevel=0.7
          }
          //console.log(response.data)
          if(response.data.invite_user='yes'){
            this.invite=response.data.invitedata
          }else {
            this.noteam=true
          }


        })
        .catch(function (error) {

        })
    },
    getDate:function(time) {
      return time.slice(0,time.indexOf('T'))
    },
    getMoney:function(viplevel) {
      let money=0
      if(viplevel==1){
        money=0
      }else if(viplevel==2){
        money=this.userlevel*99
        money=money.toFixed(1)
      }else if(viplevel==3){
        money=this.userlevel*199
        money=money.toFixed(1)
      }else if(viplevel==4){
        money=this.userlevel*399
        money=money.toFixed(1)
      }else {
        money=this.userlevel*599
        money=money.toFixed(1)
      }
      return money
    },
    gotoRetail: function () {
      this.$router.push({
        name: 'Retail',
      })
    },
    gotoPoster: function () {
      this.$router.push({
        name: 'Poster',
      })
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



