<template>
  <div id='Retail' class="page-container minebg">
    <!--顶部标题加返回-->
    <div class="subminehead">
      <a @click="gotoMine()"></a>
      <div class="submineheadtitle">推广赚钱</div>
    </div>

    <div class="detailhead">
      <div class="detailheadbox">
        <div class="detailheadimg"><img src="/static/image/mineheaduser.png"></div>
        <div class="detailheadtext"><p>{{viplevelname}}</p></div>
        <div class="detailheadlevel">推广佣金{{viplevemoney}}%</div>
      </div>
    </div>

    <div class="detailcurmoney">
      <div class="detailcurmoneybox">
        <p>可提现佣金(元)</p>
        <h3>{{money}}</h3>
      </div>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="getmoney()">
        <div class="mineunitsubimg">
          <img src="/static/icon/detail-tixian.png">
        </div>
        <div class="mineunitsubtext">我要提现</div>
      </a>
      <a class="mineunitsub" @click="gotoMoney()">
        <div class="mineunitsubimg">
          <img src="/static/icon/detail-mingxi.png">
        </div>
        <div class="mineunitsubtext">提现记录</div>
      </a>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="gotoTeam()">
        <div class="mineunitsubimg">
          <img src="/static/icon/detail-tuandui.png">
        </div>
        <div class="mineunitsubtext">我的团队</div>
      </a>
      <a class="mineunitsub" @click="gotoPoster()">
        <div class="mineunitsubimg">
          <img src="/static/icon/detail-haibao.png">
        </div>
        <div class="mineunitsubtext">推广海报</div>
      </a>
    </div>

    <div class="mineunit">
      <a class="mineunitsub" @click="showvip()">
        <div class="mineunitsubimg">
          <img src="/static/icon/icon-customerser.png">
        </div>
        <div class="mineunitsubtext">联系客服</div>
      </a>
    </div>


    <footer>备案号：京ICP备19012345号</footer>

    <div class="toast" v-show="toastShow">
      <div class="toastbox">
        <div class="toasticonbox">
          <img src="/static/icon/icon-toast.png">
        </div>
        {{toastText}}
      </div>
    </div>

    <!--客服弹窗-->
    <div v-show="showvipbox" class="popuppaybox">
      <div class="paybox">
        <svg @click="closevip()" class="payboxcloseicon"></svg>
        <div class="payboxalert">
          <p class="payboxalertvip">请扫描下方二维码添加客服微信</p>
          <!--<p class="payboxalertdes">请使用{{getpayclasstype()}}扫码支付</p>-->
          <!--<p class="payboxalertadd">请在{{timecount}}秒内完成支付，成功后5S内开通VIP</p>-->
          <div class="payboximg">
            <img src="/static/image/kefuweixin.jpg" alt="付款二维码">
            <!--<img src="/static/image/vip-loading.png" alt="付款二维码">-->
          </div>
        </div>

      </div>
    </div>

  </div>

</template>

<script>
  import qs from 'qs'
export default {
  name: 'Retail',
  data () {
    return {
      loginType:true,
      invite:[],
      recordmoney:[],
      viplevelname:'',
      viplevemoney:30,
      toastShow: false,
      toastText: '',
      showvipbox:false,
      money:0,
    }
  },
  mounted: function() {
    this.getData();
  },
  methods:{
    getData:function(){
      this.axios.get("api/retail")
        .then(response =>{
          if(response.data.loginType == 'ok'){
            this.loginType = true
          }else{
            this.loginType = false
            this.$router.push({
              name: 'Index',
            })
          }
          if(response.data.userlevel==2){
            // this.viplevelname='年费会员'
            // this.viplevemoney=0
            this.$router.push({
              name: 'Index',
            })
          }else if(response.data.userlevel==3){
            this.viplevelname='终身会员'
            this.viplevemoney=30
          }else if(response.data.userlevel==4){
            this.viplevelname="合伙人"
            this.viplevemoney=50
          }else if(response.data.userlevel==5) {
            this.viplevelname = "超级合伙人"
            this.viplevemoney = 70
          }else {
            this.$router.push({
              name: 'Index',
            })
          }
          this.invite=response.data.invitedata
          this.recordmoney=response.data.moneydata
          console.log(this.invite)
          for(let item in this.invite){
            if(this.invite[item].viplevel==1){
              this.money += 0
            }else if(this.invite[item].viplevel==2){
              this.money += 99*this.viplevemoney/100
            }else if(this.invite[item].viplevel==3){
              this.money += 199*this.viplevemoney/100
            }else if(this.invite[item].viplevel==4){
              this.money += 399*this.viplevemoney/100
            }else {
              this.money += 599*this.viplevemoney/100
            }
          }
          for(let item in this.recordmoney){
            this.money -=this.recordmoney[item].money
          }
          this.money=this.money.toFixed(1)
        })
        .catch(function (error) {

        })
    },
    gotoMoney: function () {
      this.$router.push({
        name: 'Money',
      })
    },
    gotoTeam: function () {
      this.$router.push({
        name: 'Team',
      })
    },
    gotoPoster: function () {
      this.$router.push({
        name: 'Poster',
      })
    },
    gotoMine: function () {
      this.$router.push({
        name: 'Mine',
      })
    },
    toast:function(str){
      let v = this
      v.toastText = str
      v.toastShow = true
      setTimeout(function(){
        v.toastShow = false
      }, 1500)
    },
    showvip:function(){
      this.showvipbox=true
    },
    closevip:function(){
      this.showvipbox=false
    },
    getmoney:function(){
      if(this.money>49){
        this.showvipbox=true
      }else {
        this.toast("可提现额不足50元，暂不支持提现")
      }
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



